function formatDate(date = new Date(), formatType = "nothing") {
  let hours = date.getHours(),
    minutes = date.getMinutes(),
    weekDay = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"],
    monthName = [
      "Jan",
      "Feb",
      "Mar",
      "Apr",
      "May",
      "Jun",
      "Jul",
      "Aug",
      "Sep",
      "Oct",
      "Nov",
      "Dec",
    ],
    ampm = hours >= 12 ? "pm" : "am",
    strTime,
    day;
  hours = hours % 12;
  hours = hours ? hours : 12; // the hour '0' should be '12'
  minutes = minutes < 10 ? "0" + minutes : minutes;
  strTime = hours + ":" + minutes + ampm;
  day = weekDay[date.getDay()];
  let month = monthName[date.getMonth()];

  // if the date is TODAY display only time else displat DAY,DATE
  let isToday = () => {
    switch (formatType) {
      case "time&date":
        // 6:28 pm 14 Nov, 2020
        return `${strTime} ${date.getDate()} ${month}, ${date.getFullYear()}`;
      case "time&day&date":
        // 6:28 pm Sat 14 Nov, 2020
        return `${strTime} ${day} ${date.getDate()} ${month}, ${date.getFullYear()}`;
      case "isToday":
        // 6:28 pm â€¢ Today
        return `${strTime} â€¢ Today`;
      case "checkIfToday":
        return `${date.getDate()}â€¢${month}â€¢${date.getFullYear()}`;
      default:
        // Sat 14 Nov, 2020
        return `${day} ${date.getDate()} ${month}, ${date.getFullYear()}`;
    }
  };
  return isToday();
}

export default formatDate;
